import UserAvatar from './UserAvatar.jsx';
import DescriptionBox from './DescriptionBox.jsx';

function Description(props) {


  let name = props.name ? props.name : 'Petra Marica'
  let tag = props.tag ? props.tag : '@pmarica'
  let location = props.location ? props.location : 'Salvador, Brasil'
  let avatar =  props.avatar  ? props.avatar : "https://www.flaticon.com/svg/static/icons/svg/3135/3135715.svg"

  return (   
      <div className="description">
      
      <UserAvatar avatar={ avatar } name={ name } />
      
      
      <DescriptionBox name="name" content={ name }/>
      <DescriptionBox name="tag" content={ tag } />
      <DescriptionBox name="location" content={ location } />
      
      </div>    
  );
}

export default Description;
