const colorArr = ['#0081b7', '#6303af', '#940014', '#007d89']

function color (n = colorArr.length-1) {
  let count = n
    return function nameColor() {
        if (count < 0) count = colorArr.length-1
        return colorArr[count--]
      }
 
  }


let colorNext = color()


function StatList(props) {

  let i = 0
  const datalist = props.data.length !== 0 ? props.data : [{ "id": "id_test", "label": ".test", "percentage": '--' }]
 
  return (   
    <ul className="stat-list">
      
      {datalist.map(iterator => {

        const id = iterator.id ? iterator.id.toString() : 'id-z0'+(++i);
        const label = iterator.label ? iterator.label : 'missing label'
        const percentage = iterator.percentage ? iterator.percentage : 'missing percentage'
         
        
        return (
          < li
            id={id}
            key={id}
            className="item"
            style={{ backgroundColor: colorNext() }}
          >
          <span className="label">{ label }</span>
          <span className="percentage">{percentage}%</span>
          
          </li>)
         
        })}

    </ul>
  );
}

export default StatList;