import ContactItem from './ContactItem.jsx';

function ContactList(props) {

  let followers = props.stats.followers ? props.stats.followers : 0
  let views = props.stats.views ? props.stats.views : 0
  let likes = props.stats.likes ? props.stats.likes : 0

  return (
        <ul className="stats">
          <ContactItem label = 'Followers' quantity = {followers} />
          <ContactItem label = 'Views' quantity = {views} />
          <ContactItem label = 'likes' quantity = {likes} />
      
        </ul>

  );
}

export default ContactList;
